.image-box  {
  display: flex;
  flex-direction: column;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.9);
  width: 100%;
  height: 100%;
  transition: visibility 0.3s linear, opacity 0.3s linear;
  opacity: 0;
  visibility: hidden;
}


